@media screen and (min-width: 768px) {
  .container-fluid {
    width: 100%;
  }
  .container {
    position: relative;
    /* 960, 1200, 1440 */
    max-width: 1200px;
  
    /* 고정 크기의 컨테이너는 중앙정렬 */
    margin: 0 auto;
  }
  
  .row {
    overflow: hidden;
    /* outline: 2px solid red; */
  }
  
  /* 컬럼 */
  .col-1 { width: 8.33%;}
  .col-2 { width: 16.66%;}
  .col-3 { width: 25%;}
  .col-4 { width: 33.33%;}
  .col-5 { width: 41.66%;}
  .col-6 { width: 50%;}
  .col-7 { width: 58.33%;}
  .col-8 { width: 66.66%;}
  .col-9 { width: 75%;}
  .col-10 { width: 83.33%;}
  .col-11 { width: 91.66%;}
  .col-12 { width: 100%;}
  
  /* 속성선택자 */
  [class*=col] {
    /* border: 2px solid green; */
    float: left;
    /* padding: 20px; */
  }
}
